import React from 'react';
import Carousel1 from "./Carousel1";
//import Calcutta from "./Calcutta";
import "react-responsive-carousel/lib/styles/carousel.min.css";



const Galleria = (gino) => {

//console.log(gino)





	if(window.Scrollamelo) {
		const gino1 = window.document.getElementById(`img${gino.props.currentid}`);
		if(gino1&&gino.props.modalita==="sfondogallery")	gino1.scrollIntoView()

	}













	return(
		<div	className={gino.display}>

			<div id="sfondoGallerySecond" className={"sfondoGallerySecond isplayNone overlay " + gino.display}>


			</div>

		{/*	<Calcutta type={gino.props.type} updateCurrentId={gino.updateCurrentId} currentImg={gino.props.currentid} inHeight={gino.props.inHeight} inWidth={gino.props.inWidth} robots={gino.props.robotsFil}/>
*/}
			<div>
		    <Carousel1 type={gino.props.type} updateCurrentId={gino.updateCurrentId} currentImg={gino.props.currentid} inHeight={gino.props.inHeight} inWidth={gino.props.inWidth} robots={gino.props.robotsFil}/>
		  </div>


		</div>


	);



}


export default Galleria;



